import React from 'react';
import ReactDOM from 'react-dom';
import './style.css'

import App from './App'

try {
    let page = window.location.href.split('/')[3].charAt(0).toUpperCase() + window.location.href.split('/')[3].slice(1)
    switch (page) {
        case ('Notifica%C3%A7%C3%B5es'):
            page = 'Notificações'
            break
        case ('Configura%C3%A7%C3%B5es'):
            page = 'Configurações'
            break
    }
    if (page !== 'Home' && page !== 'Notificações' && page !== 'Mensagens' && page !== 'Perfil' && page !== 'Configurações') {
        page = 'Home'
    }
    ReactDOM.render(<App page={page}/>, document.querySelector('#root'))
} catch {
    ReactDOM.render(<App />, document.querySelector('#root'))
}
